@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .btn {
    @apply inline-flex items-center justify-center rounded-2xl px-3 py-2 text-sm font-semibold shadow-soft border bg-white hover:bg-gray-50;
  }
  .btn-primary {
    @apply bg-brand-600 text-white border-transparent hover:bg-brand-700;
  }
  .btn-outline {
    @apply border-gray-200 text-gray-700 hover:bg-gray-50;
  }
  .badge {
    @apply inline-flex items-center gap-1 rounded-full px-2.5 py-1 text-xs font-semibold ring-1;
  }
  .badge-success { @apply bg-brand-50 text-brand-700 ring-brand-100; }
  .badge-warn    { @apply bg-orange-50 text-orange-700 ring-orange-200; }
  .badge-danger  { @apply bg-red-50 text-red-700 ring-red-200; }
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}
